.content-header{background:transparent;position:absolute;top:0px;display:flex;justify-content:center;align-items:center;z-index:999}@-webkit-keyframes hvr-pop{100%{-webkit-transform:scale(1.2);transform:scale(1.2)}}@keyframes hvr-pop{100%{-webkit-transform:scale(1.2);transform:scale(1.2)}}.invite-friend-button{display:inline-block;vertical-align:middle;-webkit-transform:perspective(1px) translateZ(0);transform:perspective(1px) translateZ(0);box-shadow:0 0 1px rgba(0,0,0,0);position:fixed;right:20px;bottom:150px;z-index:99999;cursor:pointer}.invite-friend-button>svg{max-width:90px;max-height:90px}.invite-friend-button:active{transform:scale(0.8);transition:transform ease-in}.invite-friend-button:hover{transform:scale(1.1);transition:transform 0.2s ease-in}
