@keyframes newsHoverIn{from{transform:translate(0, 0px)}to{transform:translate(0, -10px)}}@keyframes newsHoverOut{from{transform:translate(0, -10px)}to{transform:translate(0, 0px)}}.company-name{text-align:right;padding-right:20px;font-style:normal;font-weight:bold;font-size:14px;line-height:17px;margin-bottom:12px}.footer-right>ul>li{animation-duration:0.2s;animation-name:newsHoverOut;animation-iteration-count:1}.footer-right>ul>li:hover{animation-duration:0.2s;animation-name:newsHoverIn;animation-iteration-count:1;animation-fill-mode:forwards}.ttl-footer>svg{animation-duration:0.2s;animation-name:newsHoverOut;animation-iteration-count:1}.ttl-footer:hover>svg{animation-duration:0.2s;animation-name:newsHoverIn;animation-iteration-count:1;animation-fill-mode:forwards}.facebook-footer>svg{max-width:36px;max-height:36px}.youtube-footer>svg{max-width:36px;max-height:36px}.titktok-footer>svg{max-width:36px;max-height:36px}.stnhd-footer{margin-left:10px}.stnhd-footer>svg{width:auto;height:100px}.ttl-footer>svg{max-width:70px;max-height:60px}
